export default class Card {
  constructor({name, link, handleCardClick, likes = [], _id, owner = {}, userId, removeCard, toggleLike}, cardSelector) {
      this._name = name;
      this._link = link;
      this._cardSelector = cardSelector;
      this._handleCardClick = handleCardClick;
      this._likes = likes;
      this._id = _id;
      this._ownerId = owner._id;
      this._userId = userId;
      this._removeCard = removeCard.bind(this);
      this._toggleLike = toggleLike.bind(this);
  }

  _getTemplate() {
      const galleryElement = document
        .querySelector(this._cardSelector)
        .content
        .querySelector('.gallery__items')
        .cloneNode(true);
      
      return galleryElement
  }

  _setCountLikes(likes) {
    this._galleryElement.querySelector('.like__counter').textContent = likes.length;
  }

  _getStateLike() {
    return this._likes.find(owner => owner._id === this._userIdId);
  }
  
  _likeImg = (evt) => {
    this._toggleLike(this._id, this._likes)
    .then(res => {
      this._likes = res.likes;
      this._setCountLikes(this.likes);
      if(this._getStateLike()) {
        evt.target.classList.add('card__like_active');
      } else {
        evt.target.classList.remove('card__like_active');
      }
    })
  }

  _deleteImg() {
      this._removeCard(this._id);
      this._galleryElement.remove();
      this._galleryElement = null;
  }

  generateCard() {
      this._galleryElement = this._getTemplate()
      this._cardImage = this._galleryElement.querySelector('.card__image');
      this._cardName = this._galleryElement.querySelector('.card__name');
      this._cardLike = this._galleryElement.querySelector('.card__like');
      this._cardTrash = this._galleryElement.querySelector('.card__trash');

      const cardLikes = this._galleryElement.querySelector('.like__counter');
      this._cardImage.src = this._link;
      this._cardImage.alt = this._name;
      this._cardName.textContent = this._name;
      cardLikes.textContent = this._likes.length;

      this._setEventListeners()

      if(!this._ownerId !== this.userId) {
        this._cardTrash.remove()
      }
      
      return this._galleryElement;
  }

  _setEventListeners() {
      this._cardLike.addEventListener('click', (evt) => {
        this._likeImg(evt);
      });
      this._cardTrash.addEventListener('click', (evt) => {
        this._deleteImg(evt);
      });
      this._cardImage.addEventListener('click', () => {
        this._handleCardClick();
      });
    }
}