import '../pages/index.css';

import Card from '../components/Card.js'
import FormValidator from '../components/FormValidator.js';
import Section from '../components/Section.js'
import PopupWithImage from '../components/PopupWithImage.js';
import PopupWithForm from '../components/PopupWithForm.js';
import UserInfo from '../components/UserInfo.js';
import Api from '../components/Api';
// Объявление констант
import {
  initialCards,
  selectors, 
  galleryTemplate,
  popupEdit,
  popupEditOpen,
  profileName,
  profileProfession,
  inputEditName,
  inputEditProfession,
  popupAdd,
  popupAddOpen,
  popupAddForm,
  gallerySection,
  popupFs,
  formValidators,
  apiConfig,
  profileAvatar,
} from '../utils/constants.js';

const api = new Api(apiConfig);

const enableValidationForms = () => {
  const forms = Array.from(document.forms);
  forms.forEach(form => {
    const formValidator = new FormValidator(selectors, form);
    const formName = form.getAttribute('name');
    formValidators[formName] = formValidator;
    formValidator.enableValidation();
  });
};

enableValidationForms();

const popupWithImage = new PopupWithImage(popupFs);
popupWithImage.setEventListeners();

const createCard = ({name, link, likes, _id, owner}) => {
    const card = new Card({
      name,
      link,
      likes,
      _id,
      owner,
      toggleLike,
      removeCard,
      handleCardClick: () => {
        popupWithImage.open({name, link});
      }
    }, galleryTemplate
    );
  const galleryElement = card.generateCard();
  return galleryElement
};

const cardList = new Section({
  items: null,
  renderer: (cardItem) => {
    const cardElement = createCard(cardItem);
    cardList.addItem(cardElement);
  }
},
  gallerySection
);

api.getInitialCards()
.then(res => {
  const dataCards = res.map(data => {
    return {
      name: data.name,
      link: data.link,
      likes: data.likes,
      _id: data._id,
      owner: data.owner,
    }
  })
  cardList.setInitialArray(dataCards);
  cardList.renderItems()
})


const userInfo = new UserInfo({
  profileNameSelector: profileName,
  profileProfessionSelector: profileProfession,
  profileAvatarSelector: profileAvatar
});

api.getUser()
.then(data => {
  userInfo.initialize(data)
});

const removeCard = (id) => {
  api.removeCard(id);
}

const setLikeCard = (idCard) => {
  return api.addLike(idCard);
};

const removeLikeCard = (idCard) => {
  return api.removeLike(idCard);
}

const toggleLike = (idCard, likes) => {
  const stateLike = likes.find(owner => owner._id === userInfo._id);
  if(!stateLike) {
    return setLikeCard(idCard);
  } else {
    return removeLikeCard(idCard);
  }
}

const popupEditProfile = new PopupWithForm({
    initializeForm: () => {
      const userData = userInfo.getUserInfo();
      inputEditName.value = userData.name;
      inputEditProfession.value = userData.profession;
      formValidators['popupEditForm'].resetValidation();
      },
    handleSubmit: evt => {
      evt.preventDefault();

      const inputValues = popupEditProfile.getInputValues();
      api.setUser({name: inputValues.name, about: inputValues.profession})
      .then(res => {
        userInfo.setUserInfo(inputValues);
        popupEditProfile.close();
      });
    }
  },
  popupEdit
);
popupEditProfile.setEventListeners();
popupEditOpen.addEventListener('click', popupEditProfile.open.bind(popupEditProfile));

const popupAddCard = new PopupWithForm({
    initializeForm: () => {
      formValidators['popupAddForm'].resetValidation();
    },
    handleSubmit: evt => {
      evt.preventDefault();
      
      const inputValues = popupAddCard.getInputValues();
      const cardItem = {
        name: inputValues['description'],
        link: inputValues['link'],
      };
      
      api.createCard(cardItem)
      .then(res => {
        cardItem._id = res._id;
        cardItem.owner = res.owner
        const cardElement = createCard(cardItem);
        cardList.addItem(cardElement)
        popupAddCard.close();
      })
    }
  },
  popupAdd,
);
popupAddCard.setEventListeners();
popupAddOpen.addEventListener('click', popupAddCard.open.bind(popupAddCard));